import styled from 'styled-components';

const HeaderStyle = styled.section`
  margin: 0px auto;
  padding: 0;
  border: 0;
  width: calc(100% - 60px);
  height: 100%;
  max-width: 1740px;
  position: relative;
  display: block;
  .scrolling-navbar {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
  }
  .menu-link {
    color: #17191d !important;
    font: bold 14px/14px 'Roboto', sans-serif;
    text-decoration: none;
  }

  .btn {
    cursor: pointer;
    color: rgb(255, 255, 255);
    display: inline-block;
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
    font-weight: bold;
    letter-spacing: 1px;
    line-height: 14px;
    position: relative;
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap;
    max-width: 100%;
    background: #ff3366;
    border-width: 2px;
    border-style: solid;
    border-color: #ff3366;
    border-image: initial;
    border-radius: 100em;
    outline: 0px;
    overflow: hidden;
    padding: 13px 32px 11px;
    text-decoration: none;
    transition: all 0.2s ease 0s;
  }
  .btn:hover {
    color: #e81e61;
    background: #ffffff;
    border-color: #e81e61;
  }
`;

export default HeaderStyle;
